import React from "react";
import "./styles/tailwind.css";
wp.blocks.registerBlockType("tsgutenblock/block-type", {
    title: "TypesScript GurenBerg Block Creating",
    icon: "smiley",
    category: "common",
    attributes: {
        skyColor: { type: "string" },
        grassColor: { type: "string" },
    },
    edit: function (props) {
        // const updateSkyColor = (e: React.SyntheticEvent) => {
        //   props.setAttributes({ skyColor: (e.target as HTMLTextAreaElement).value });
        // };
        // const updateGrassColor = (e: React.SyntheticEvent) => {
        //   props.setAttributes({ grassColor: (e.target as HTMLTextAreaElement).value });
        // };
        return (React.createElement(React.Fragment, null,
            React.createElement("div", { className: "bg-red-700", style: { backgroundColor: 'yellow' } },
                React.createElement("input", { type: "text", name: "skycolor", id: "", placeholder: "sky color", value: props.attributes.skyColor, onChange: function (e) { return props.setAttributes({ skyColor: e.target.value }); } }),
                React.createElement("input", { type: "text", value: props.attributes.grassColor, name: "grasscolor", id: "", placeholder: "grass color", onChange: function (e) {
                        return props.setAttributes({ grassColor: e.target.value });
                    } }))));
    },
    save: function (props) {
        // return null;
        return (React.createElement(React.Fragment, null,
            React.createElement("p", { style: { backgroundColor: 'yellow' } },
                "The sky color is ",
                props.attributes.skyColor,
                " and grass color is",
                ' ',
                props.attributes.grassColor)));
    },
});
